@import "_header";
@import "_card";

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  color: #000;
  font-size: 11px;
  line-height: 13px;
  background-color: #fff;
}

.main {
  width: 944px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  grid-column-gap: 48px;
  grid-row-gap: 50px;
  margin: 50px auto 78px;
}
